import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { ICliente } from '../../models/cliente';
import { ClientesService } from '../../service/clientes.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { finalize, firstValueFrom, Subject, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { ILocalidad } from '@shared/models/localidad';
import { ICondicionIva } from '@shared/models/condicioniva';
import { AuthService } from '@core/auth/auth.service';
import { IAlertConfig } from '@shared/models/alert';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
    selector: 'app-save-clientes',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss']
})
export class SaveComponent implements OnInit {
    form: FormGroup;
    cliente: ICliente;
    modoVisualizacion: boolean = false;
    title: string;
    localidades: ILocalidad[] = [];
    condiciones: ICondicionIva[] = [];
    clientes: ICliente[] = [];
    tieneClienteAsociado: boolean = false;
    selectedOption: string = '';
    isFisicaDisabled: boolean = false;
    isJuridicaDisabled: boolean = false;
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _empresaService: EmpresaService,
        private _clientesService: ClientesService,
        private _authService: AuthService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.cliente = this.data ? this.data : null;
        if (this.data && this.data.visualizar) {
            this.title = 'Ver Cliente';
            this.modoVisualizacion = true;
        } else {
            this.title = this.cliente?.codigo ? 'Editar cliente' : 'Nuevo cliente';
        }
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
        .pipe(takeUntil(this._unsubscribeAll))
        .subscribe(empresa => {
            this.color_primario = empresa.color_primario;
            this.color_secundario = empresa.color_secundario;
        });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.getLocalidades();
        this.getCondicionesIva();
        this.getClientes();
        this.verificarClienteAsociado();
        this.getTipoCliente();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
          next: (config: IAlertConfig) => {
            this.showAlert = true;
            this._alertConfig = {
              ...config,
              appearance: 'border',
            };
            let time = 2;
            timer(100, 1500)
              .pipe(
                finalize(() => {
                  this.showAlert = false;
                  this._alertConfig = null;
                }),
                takeWhile(() => time > 0),
                takeUntil(this._unsubscribeAll),
                tap(() => (time -= 1))
              )
              .subscribe();
          },
        });
    }

    /**
     * Esta funcion es para verificar si la empresa tiene cliente asociado
     */
    verificarClienteAsociado() {
        this._authService.verificarDatosEmpresaCargados().subscribe((empresaCargada) => {
            if (empresaCargada && this._authService.empresaData.cliente_asociado === 1) {
                this.tieneClienteAsociado = true;
            }
        });
    }
    
    /**
     * Esta funcion es para ver que Tipo de Cliente
     */
    getTipoCliente() {
        if (this.cliente) {
            if (this.cliente.tipoCliente === 0) {
                this.selectedOption = 'juridica';
                this.isFisicaDisabled = true;
            } else if (this.cliente.tipoCliente === 1) {
                this.selectedOption = 'fisica';
                this.isJuridicaDisabled = true;
            }
            this.toggleForm(this.selectedOption, this.cliente.tipoCliente);

            if (this.cliente.codigo && !this.cliente.cliente) {
                this.form.get('cliente').setValue('sin_asociado');
            }
        }
    }

    toggleForm(option: string, tipo: number) {
        this.selectedOption = option;
        if (tipo === 0) {
            this.form = new FormGroup({ 
                razonSocial: new FormControl((this.cliente ? this.cliente.razonSocial : ''), Validators.required),
                DNI: new FormControl((this.cliente ? this.cliente.DNI : null), Validators.required),
                Cuit: new FormControl((this.cliente ? this.cliente.Cuit : ''), Validators.required),
                condicionIva: new FormControl((this.cliente ? this.cliente.condicionIva : ''), Validators.required),
                direccion: new FormControl((this.cliente ? this.cliente.direccion : ''), Validators.required),
                localidad: new FormControl((this.cliente ? this.cliente.localidad : ''), Validators.required),
                telefonoParticular: new FormControl((this.cliente ? this.cliente.telefonoParticular : ''), Validators.required),
                telefonoMovil: new FormControl((this.cliente ? this.cliente.telefonoMovil : ''), Validators.required),
                correoElectronico: new FormControl((this.cliente ? this.cliente.correoElectronico : ''), [Validators.required, Validators.email]),
            })

            this.form.get('DNI').disable();

            if (!this.tieneClienteAsociado) {
                this.form.addControl('contacto_referencia', new FormControl((this.cliente ? this.cliente.contacto_referencia : ''), Validators.required));
            } else {
                this.form.addControl('cliente', new FormControl((this.cliente ? this.cliente.cliente : ''), Validators.required));
            }
        } else if (tipo === 1) {
            this.form = new FormGroup({ 
                razonSocial: new FormControl((this.cliente ? this.cliente.razonSocial : ''), Validators.required),
                DNI: new FormControl((this.cliente ? this.cliente.DNI : ''), Validators.required),
                Cuit: new FormControl((this.cliente ? this.cliente.Cuit : ''), Validators.required),
                condicionIva: new FormControl((this.cliente ? this.cliente.condicionIva : ''), Validators.required),
                direccion: new FormControl((this.cliente ? this.cliente.direccion : ''), Validators.required),
                localidad: new FormControl((this.cliente ? this.cliente.localidad : ''), Validators.required),
                telefonoParticular: new FormControl((this.cliente ? this.cliente.telefonoParticular : ''), Validators.required),
                telefonoMovil: new FormControl((this.cliente ? this.cliente.telefonoMovil : ''), Validators.required),
                correoElectronico: new FormControl((this.cliente ? this.cliente.correoElectronico : ''), [Validators.required, Validators.email]),
            })

            this.form.get('DNI').enable();

            if (!this.tieneClienteAsociado) {
                this.form.addControl('contacto_referencia', new FormControl((this.cliente ? this.cliente.contacto_referencia : ''), Validators.required));
            } else {
                this.form.addControl('cliente', new FormControl((this.cliente ? this.cliente.cliente : ''), Validators.required));
            }
        }
    }

    /**
     * Esta funcion la utilizamos cuando queremos listar elementos en un selector en este caso Localidades.
     */
    async getLocalidades() {
        const localidades = await firstValueFrom(this._clientesService.getLocalidades())
        this.getDataLocalidades(localidades);
    }

    async getDataLocalidades(data) {
        this.localidades = data.list;
        // Este if es para que lo tome al momento de edicion
        if (this.cliente && this.cliente.localidad) {
            this.form.get('localidad').setValue(this.cliente.localidad.toString());
        }
    }

    /**
     * Esta funcion la utilizamos cuando queremos listar elementos en un selector en este caso Condicion Iva.
     */
    async getCondicionesIva() {
        const condicion_iva = await firstValueFrom(this._clientesService.getCondicionesIva())
        this.getDataCondiciones(condicion_iva);
    }

    async getDataCondiciones(data) {
        this.condiciones = data.list;
        // Este if es para que lo tome al momento de edicion
        if (this.cliente && this.cliente.condicionIva) {
            this.form.get('condicionIva').setValue(this.cliente.condicionIva.toString());
        }
    }

    /**
     * Esta funcion la utilizamos cuando queremos listar los elementos en un selector en este caso Clientes
     */
    async getClientes() {
        const Clientes: any = await firstValueFrom(this._clientesService.getClientes());
        if (this.cliente && this.cliente.codigo) {
            this.clientes = Clientes.list.filter(c => c.codigo !== this.cliente.codigo);
        } else {
            this.clientes = Clientes.list;
        }
    }
    

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            let payload = null;
            if (this.selectedOption === 'juridica'){
                
                payload = {
                    codigo: this.cliente?.codigo?this.cliente.codigo : null,
                    razonSocial: this.form.get('razonSocial').value,
                    Cuit: this.form.get('Cuit').value,
                    condicionIva: this.form.get('condicionIva').value,
                    direccion: this.form.get('direccion').value,
                    localidad: this.form.get('localidad').value,
                    telefonoParticular: this.form.get('telefonoParticular').value,
                    telefonoMovil: this.form.get('telefonoMovil').value,
                    correoElectronico: this.form.get('correoElectronico').value,
                    tipoCliente: 0
                }
                if (!this.tieneClienteAsociado) {
                    payload['contacto_referencia'] = this.form.get('contacto_referencia').value;
                } else {
                    if (this.form.get('cliente').value === 'sin_asociado') {
                        payload['cliente'] = null; 
                    } else {
                        payload['cliente'] = this.form.get('cliente').value;
                    }
                }
            } else if (this.selectedOption === 'fisica') {
                payload = {
                    codigo: this.cliente?.codigo?this.cliente.codigo : null,
                    razonSocial: this.form.get('razonSocial').value,
                    DNI: this.form.get('DNI').value,
                    Cuit: this.form.get('Cuit').value,
                    condicionIva: this.form.get('condicionIva').value,
                    direccion: this.form.get('direccion').value,
                    localidad: this.form.get('localidad').value,
                    telefonoParticular: this.form.get('telefonoParticular').value,
                    telefonoMovil: this.form.get('telefonoMovil').value,
                    correoElectronico: this.form.get('correoElectronico').value,
                    tipoCliente: 1
                }
                if (!this.tieneClienteAsociado) {
                    payload['contacto_referencia'] = this.form.get('contacto_referencia').value;
                } else {
                    if (this.form.get('cliente').value === 'sin_asociado') {
                        payload['cliente'] = null; 
                    } else {
                        payload['cliente'] = this.form.get('cliente').value;
                    }
                }
            }
            if (this.cliente?.codigo) {
                this._clientesService.updateCliente(payload)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.editadoExitosamente.emit();
                                this.onCloseMenu();
                            }
                        }
                    });
            } else {
                this._clientesService.saveCliente(payload)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.creadoExitosamente.emit();
                                this.onCloseMenu();
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}