import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { ICliente } from '../models/cliente';
import { AuthService } from '@core/auth/auth.service';
import { Response } from '@shared/models/response-with-token';
import { Response as localidadResponse } from '@shared/models/localidad';
import { Response as condicionResponse } from '@shared/models/condicioniva';

@Injectable()
export class ClientesService {
    constructor(private http: HttpClient, private _authService:AuthService) { }
    /**
     * Funcion en el servicio
     * - Esta funcion retorna los Clientes.
     * @returns Rubros
     */
    getClientes(sort?:any): Observable<Response[]> {
        let f = '?';
        f += sort == 'desc' ? 'sort=-descripcion' : 'sort=descripcion';
        let subject = new Subject<any>();

        this.http.get<Response[]>(`${environment.administracion.view_clientes}${f}`)
        .subscribe((resp:any) => {
            this._authService.accessToken = resp.token,
            subject.next({
                'list':resp.result,
                // 'totalPages': resp.headers.get('x-pagination-page-count'),
                // 'totalCount': resp.headers.get('x-pagination-total-count')
            })
        });
        return subject.asObservable();
    }

    /**
     * Funcion en el Servicio 
     * @param cliente 
     * @returns Crea un item nuevo
     */
    saveCliente(cliente: ICliente): Observable<ICliente> {
        return this.http.post<ICliente>(`${environment.administracion.clientes}`, cliente);
    }

    /**
     * Funcion en el servicio
     * @param cliente
     * @returns Item actualizado actualizado
     */
    updateCliente(cliente: ICliente): Observable<ICliente> {
        return this.http.put<ICliente>(`${environment.administracion.clientes}` + '/' + cliente.codigo, cliente);
    }

    /**
     * Funcion en el servicio
     * @param idcliente 
     * @returns Elimina un item
     */
    deleteCliente(idcliente: number): Observable<ICliente> {
        return this.http.delete<ICliente>(`${environment.administracion.clientes}` + '/' + idcliente);
    }

    /**
     * Listamos las localidades para el selector del formulario de alta/edicion
     * @returns Todos las localidades.
    */
    getLocalidades() {
        let subject = new Subject<any>();
        this.http.get<localidadResponse[]>(`${environment.localidades}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    'list': resp.result,
                })
            });
        return subject.asObservable();
    }

    /**
     * Listamos las condicion iva para el selector del formulario de alta/edicion
     * @returns Todas las condicion iva.
     */
    getCondicionesIva() {
        let subject = new Subject<any>();
        this.http.get<condicionResponse[]>(`${environment.administracion.condicion_iva}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    'list': resp.result,
                })
            });
        return subject.asObservable();
    }
}